.table {
  margin: 1rem 0;

  width: 100%;
  border-collapse: collapse;

  > tr > th,
  > thead > tr > th {
    padding: 0.5rem;

    color: black;
    font-weight: 600;

    font-size: bold;
    text-align: left;
  }

  > tr > th,
  > thead > tr:last-child > th {
    border-bottom: 1px solid rgb(230, 230, 230);
    vertical-align: middle;
  }

  > tr > td,
  > tbody > tr > td {
    padding: 0.5rem;
    vertical-align: middle;
  }

  > tr + tr > td,
  > tbody > tr + tr > td {
    border-top: 1px solid rgb(230, 230, 230);
  }

  > tfoot > tr:first-child > td {
    border-top: 2px solid rgb(230, 230, 230);
  }

  > tfoot > tr > td {
    padding: .5rem;
    padding-top: 1rem;
    vertical-align: middle;
  }

  > tr > th:first-child,
  > thead > tr > th:first-child,
  > tr > td:first-child,
  > tbody > tr > td:first-child,
  > tfoot > tr > td:first-child {
    padding-left: 0;
  }

  > tr > th:last-child,
  > thead > tr > th:last-child,
  > tr > td:last-child,
  > tbody > tr > td:last-child,
  > tfoot > tr > td:last-child {
    padding-right: 0;
  }

  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.table-total {
  font-weight: 600;
}
