body {
  min-height: 100vh;

  display: flex;
  flex-direction: column;

  font-family: sans-serif;
  line-height: 1.5;
}

.site-header {
  color: white;
  background: #1b212f;
}

.site-footer {
  border-top: 1px solid #e6e6e6;
}

.container {
  flex: 1;

  display: flex;
  flex-direction: row;
}

.sidebar {
  background: white;

  border-right: 1px solid #e6e6e6;
}

.content {
  flex: 1;

  padding: 2rem;

  background: #fafafa;
}

.card {
  margin: 2rem 0;
  padding: 2rem;
  background: #fff;
  border-radius: .12rem;
  box-shadow: rgba(0, 0, 0, .12) 0px 1px 3px, rgba(0, 0, 0, .24) 0px 1px 2px;
}

.heading-1,
.heading-2,
.heading-3,
.heading-4,
.heading-5,
.heading-6 {
  margin: 2rem 0;

  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    margin-top: 0;
  }
}

.heading-1 {
  font-size: 1.7rem;
}

.heading-2 {
  font-size: 1.2rem;
  font-weight: bold;
}

.heading-3 {
  font-size: 1.2rem;
}

.heading-4 {
  font-size: 1.2rem;
  font-style: italic;

  margin: 2rem 0;
  margin-bottom: 1rem;
}

.heading-monospaced {
  font-family: monospace;
}

.para {
  margin: 1rem 0;

  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    margin-top: 0;
  }
}

.method {
  display: inline-block;

  padding: 0 .25rem;
  margin-right: 1rem;

  color: white;
  background: rgb(0, 100, 206);

  border-radius: .25rem;
}

pre {
  font-family: monospace;
  white-space: pre;
}

code {
  font-family: monospace;
}

.well {
  display: block;

  padding: 0.5rem 1rem;
  background: #fafafa;

  border: 1px solid #e6e6e6;
  border-radius: 0.25rem;
}

a:link,
a:active,
a:visited {
  color: rgb(0, 100, 206);
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

article {
  margin: 4rem 0;

  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    margin-top: 0;
  }
}
